.navbar{
    background-color: rgb(93, 177, 171);
}

.navbar ul{
    margin:0;
    padding:5px 0;
    list-style: none;
}

.navbar li{
    color:white;
    padding:0 40px;
    margin:0;
    list-style-type: none;
    display:inline;
}

.navbar button{
    color:white;
    cursor: pointer;
    padding:6px 10px;
    outline:none;
    font-size: medium;
    border:none;
    border-radius: 4px;
    background-color: inherit;
}

.navbar button:hover{
    color:rgb(10, 61, 59);
}