@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }

.data-box * {
    box-sizing: border-box;
  }

.card{
    text-align: center;
    padding: 0 10px;
    border:1px solid rgb(236, 235, 235);
    border-radius: 4px;
    box-shadow: 0 0px 6px 0px rgba(100, 100, 100, 0.4);
}

.card h2{
    text-transform: uppercase;
    color:lightcoral;
}

.card p{
    color:darkcyan;
    font-size: 20px;
}

.column{
    width:25%;
    float:left;
    padding:0 10px;
}

.data-box{
    margin:0 100px;
}

.row{
    margin: 40px 0;
}
.row:after {
    content: "";
    display: table;
    clear: both;
  }
.header{
    text-align: center;
    padding:20px;
    margin:10px auto;
}

.search-box{
    width:40%;
    color:rgb(94, 94, 94);
    padding:10px;
    font-size: large;
    border-radius: 4px;
    border:none;
    background-color: rgba(32, 178, 171, 0.377);
    outline:none;
}

.header button{
    color:white;
    border-radius: 4px;
    border:none;
    padding:10px;
    font-size: large;
    outline:none;
    cursor: pointer;
    background-color: rgb(32, 178, 171);
}
.header button:active{
    background-color: rgb(224, 224, 224);
}

.navbar{
    background-color: rgb(93, 177, 171);
}

.navbar ul{
    margin:0;
    padding:5px 0;
    list-style: none;
}

.navbar li{
    color:white;
    padding:0 40px;
    margin:0;
    list-style-type: none;
    display:inline;
}

.navbar button{
    color:white;
    cursor: pointer;
    padding:6px 10px;
    outline:none;
    font-size: medium;
    border:none;
    border-radius: 4px;
    background-color: inherit;
}

.navbar button:hover{
    color:rgb(10, 61, 59);
}
.footer{
    overflow: hidden;
    color:grey;
    background-color: rgb(0, 24, 22);
    width:100%;
    text-align: center;
    bottom:0px;
    position: absolute;
}

.footer a{
    text-decoration: none;
    color:lightgrey;
}
